* {
    padding: 0;
    border: none;
    outline: none;
}

body {
    color: #18dcff;
}
.contact-form {
    margin: 3rem auto 0;
    max-width: 50%;
    max-width: 400px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    border: 1px solid #18dcff;
    background: rgba(255, 255, 255, 0.61);
    padding: 0 30px 10px;
    box-shadow: 0px 4px 10px rgba(135, 248, 212, 0.64);
}
h2 {
    margin-top: 35px;
    text-align: center;
}
h4 {
    text-align: center;
}

.form-content {
    display: grid;
    grid-template-rows: repeat(4, 1fr) 105px;
}
input,
textarea {
    background: none;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 1.1rem;
    border-bottom: 1px solid #008bff;
    color: #18dcff;
}
input::placeholder,
textarea::placeholder {
    color: rgb(190, 188, 188);
}
input,
.email-content,
textarea {
    width: 90%;
    margin: 0 auto;
}
input {
    padding: 24px 0 12px;
}
.email-content {
    position: relative;
}
.email-content input {
    width: 100%;
}
#not-mail {
    display: none;
    position: absolute;
    top: 4px;
    color: #ff4d4d;
    right: 0;
    transform-origin: 50% 50%;
}
@keyframes dongle {
    0% {
        transform: translate(0px, 0px);
    }
    10% {
        transform: translate(-10px, 0px);
    }
    20% {
        transform: translate(10px, 0px);
    }
    30% {
        transform: translate(-10px, 0px);
    }
    40% {
        transform: translate(10px, 0px);
    }
    50% {
        transform: translate(-10px, 0px);
    }
    60% {
        transform: translate(10px, 0px);
    }
    70% {
        transform: translate(-10px, 0px);
    }
    80% {
        transform: translate(10px, 0px);
    }
    90% {
        transform: translate(-10px, 0px);
    }
    100% {
        transform: translate(0px, 0px);
    }
}
textarea {
    resize: none;
    padding: 24px 0;
}
.button {
    font-size: 1.3rem;
    margin-top: 15px;
    color: #0eff00;
    border: none;
    cursor: pointer;
    transition: .2s;
    width: 100%;
}
.button:hover {
    letter-spacing: 2px;
}
.error::placeholder {
    color: #ff4d4d;
}
.form-message {
    margin-top: 10px;
    padding: 12px;
    opacity: 0;
    transition: 0.2s ease;
    color: black;
    border-radius: 4px;
    box-shadow: 0 0 2px rgba(51, 51, 51, 0.3);
}

.form-container {
    margin: 100px auto;
    width: 80%;
    box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 7px 20px 0 rgba(0, 0, 0, 0.2);
    position: relative;
    border-radius: 10px;
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.close-btn {
    position: absolute;
    top: 2%;
    right: 3%;
    font-size: 1.5rem;
    z-index: 1;
    color: #fff;
    cursor: pointer;
}

.form-content-left {
    background: linear-gradient(
            90deg,
            rgb(39, 176, 255) 0%,
            rgb(0, 232, 236) 100%
    );
    border-radius: 10px 0 0 10px;
    position: relative;
}

.form-img {
    width: 100%;
    height: 85%;
    position: absolute;
    top: 50%;
    left: 40%;
    transform: translate(-50%, -50%);
}

.form-img-2 {
    width: 60%;
    height: 60%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.form-success {
    text-align: center;
    font-size: 24px;
    margin-top: 80px;
    color: #fff;
}

.form-content-right {
    border-radius: 0 10px 10px 0;
    position: relative;
    background: linear-gradient(90deg, rgb(40, 40, 40) 0%, rgb(17, 17, 17) 100%);
}

.form {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 90%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.form h1 {
    font-size: 1rem;
    text-align: start;
    width: 80%;
    margin-bottom: 1rem;
    color: #fff;
}

.form-inputs {
    margin-bottom: 0.5rem;
    width: 80%;
}

.form-inputs p {
    font-size: 0.8rem;
    margin-top: 0.5rem;
    color: #f00e0e;
}

.form-label {
    display: inline-block;
    font-size: 0.8rem;
    margin-bottom: 6px;
    color: #fff;
}

.form-input {
    display: block;
    padding-left: 10px;
    outline: none;
    border-radius: 6px;
    height: 40px;
    width: 100%;
    border: none;
    background-color: white;
}

.form-input-text {
    display: block;
    padding-left: 10px;
    outline: none;
    border-radius: 2px;
    height: 100px;
    width: 100%;
    border: none;
    background-color: white;
}

.form-input::placeholder {
    font-size: 13px;
}

.form-input-text::placeholder {
    font-size: 12px;
}

.form-input-btn {
    width: 80%;
    height: 50px;
    margin-top: 10px;
    border-radius: 2px;
    background: linear-gradient(
            90deg,
            rgb(39, 176, 255) 0%,
            rgb(0, 232, 236) 100%
    );
    outline: none;
    border: none;
    color: #fff;
    font-size: 1rem;
}

.form-input-btn:hover {
    cursor: pointer;
    background: linear-gradient(
            90deg,
            rgb(39, 143, 255) 0%,
            rgb(12, 99, 250) 100%
    );
    transition: all 0.4s ease-out;
}

.form-input-login {
    font-size: 0.8rem;
    margin-top: 10px;
    color: #fff;
    width: 80%;
    text-align: center;
}

.form-input-login a {
    text-decoration: none;
    color: #27cdff;
    font-weight: 600;
}

.espace{
    margin-left: 18%;
    margin-top: 4%;
}
@media screen and (max-width: 960px) {
    .form-container{
        display: block;
    }
}
